import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import React from 'react';
import {Component} from 'react';

class Navigation extends Component {

  render(){
  return(
  <Navbar bg="light" variant="light">
  <Navbar.Brand href="/#/">Roshan Jamkatel</Navbar.Brand>
  <Nav className="mr-auto">
    <Nav.Link href="personalwebsite/#/about">About</Nav.Link>
    <Nav.Link href="personalwebsite/#/projects">Projects</Nav.Link>
    <Nav.Link href="personalwebsite/#/links">Links</Nav.Link>
  </Nav>
  </Navbar>
  )
  }
}

export default Navigation;